var {log} = console
import {publicing} from "@/api/api.js"
import {wxloginurl} from '@/api/request.js'
// 引入提示组件
import HMmessages from "@/components/HM-messages/HM-messages.vue"
//引入类
let logins = require('../login/login.js')
export default{
	components: {HMmessages},
	data() {
		return {
			modaishow: false
		}
	},
	
	methods:{
		// 显示
		init(){
			this.modaishow = true
		},
		// 取消
		messcancel(){
			this.modaishow = false
		},
		
		// 登录
		getUserInfo(event){
			this.modaishow = false
			let wxing = event.detail.userInfo
			// 实例化类
			const logclass = new logins(wxing)
			// 实例化类之后。可以访问类中的方法和属性
			logclass.listing()
			.then((res)=>{
				log(res)
				let succ = '登录成功'
				let ico = 'success'
				this.tIps(succ,ico)
			})
			.catch((err)=>{
				log(err)
			})
			
		},
		
		// 提示
		tIps(succ,ico){
			this.HMmessages.show(succ,{icon:ico,fontColor:"#ffffff",iconColor:"#ffffff",background:"rgba(102,0,51,0.8)"})
		}
	}
}
